body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  height: 50vh;
  grid-auto-flow: row;
  text-align: center;
}

img {
  width: 100%;
  height: auto;
}

.item {
  background-color: #eee;
  border: #fff solid 1px;
  float: right;
  overflow: hidden;
}

/* row 1 */

.item.hover#item1 {
  background-color: #091898;
  grid-column: 1 / 3;
  grid-row: 1 / 3;
  -webkit-transition: all .65s ease;
  transition: all .65s ease;
  -webkit-animation: grow .75s ease-out;
          animation: grow .75s ease-out;
}

